import React from 'react'
import ExternalLink from './ExternalLink'

interface ProjectEntryProps {
    title: string
    url?: string
    linkTitle?: string
    description?: string
}

const ProjectEntry: React.FC<ProjectEntryProps> = ({
    title,
    url,
    linkTitle,
    description,
}) => {
    return (
        <div style={{ marginBottom: '1.5rem' }}>
            <h3
                style={{
                    marginRight: '0.75rem',
                    marginBottom: '0.5rem',
                    display: 'inline-block',
                }}
            >
                {title}
            </h3>
            {url && <ExternalLink href={url}>{linkTitle || url}</ExternalLink>}
            <div>{description}</div>
        </div>
    )
}

export default ProjectEntry
